import {
	getArticle,
	getFeedArticle,
	addFavorite,
	deleteFavorite,
} from "@/api/article";
import { getTags } from "@/api/tag";
import { mapState } from "vuex";

export default {
	name: "HomeIndex",
	watchQuery: ["page", "tag", "tab"], //监听query中参数页面改变出发asyncData等方法，不然页面改变不会重新获取数据
	// 需要满足SEO，因此使用asyncData
	async asyncData({ query, store }) {
		const page = Number.parseInt(query.page || 1);
		const limit = 20;
		// const { data } = await getArticle({
		// 	limit,
		// 	page: (page - 1) * limit,
		// });
		// const { data: tagData } = await getTags();
		const { tag } = query;
		const tab = query.tab || "global_feed";
		const loadArticle =
			store.state.user && tab === "your_feed"
				? getFeedArticle
				: getArticle; //判断获取文章列表还是用户关注列表
		const [articleRes, tagRes] = await Promise.all([
			loadArticle({
				limit,
				page: (page - 1) * limit,
				tag,
			}),
			getTags(),
		]);
		// console.log(articleRes, tagRes);
		const { articles, articlesCount } = articleRes.data;
		articles.forEach((item) => {
			item.isDisabled = false;
		});
		const { tags } = tagRes.data;
		return {
			articles,
			articlesCount,
			limit,
			page,
			tags,
			tag,
			tab,
		};
	},
	//监听属性 类似于data概念
	computed: {
		...mapState(["user"]),
		totalPage() {
			return Math.ceil(this.articlesCount / this.limit);
		},
	},
	methods: {
		async onFavorite(article) {
			article.isDisabled = true;
			if (article.favorited) {
				// 取消点赞
				await deleteFavorite(article.slug);
				article.favorited = false;
				article.favoritesCount += -1;
			} else {
				// 添加点赞
				await addFavorite(article.slug);
				article.favorited = true;
				article.favoritesCount += 1;
			}
			article.isDisabled = false;
		},
	},
};
